import { Component, OnInit } from '@angular/core';
import { ChatService } from '../../../services';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-chat4-dashboard',
  templateUrl: './chat4-dashboard.component.html',
  styleUrls: ['./chat4-dashboard.component.scss']
})

export class Chat4DashboardComponent implements OnInit {
  messages: Observable<any>;
  user: { photo: string };
  constructor(private service: ChatService) {}

  ngOnInit() {
    this.showMessage();
  }

  showMessage() {
    this.messages = this.service.routerChat('chat4');
    this.service.user(3).subscribe((data: string[]) => this.user = { photo: data[3]});
  }

}
